<template>
  <div class="ke_da">
    <!-- =====课程头部===== -->
    <div :class="flag?'box_top':''">
      <van-nav-bar title="课程详情" left-text="返回" right-text="按钮" left-arrow @click-left="onClickLeft">
        <template #right>
          <div class="iconfont icon-fenxiang"  @click="xiao"></div>
          <!-- <van-icon name="search" size="18" /> -->
        </template>
        <template #title>
          <div v-show="!flag" class="xiangqing">课程详情</div>
          <div class="dong_ke">
            <div v-show="flag" class="dong_cheng">课程详情</div>
            <div v-show="flag" class="dong_cheng">课程大纲</div>
            <div v-show="flag" class="dong_cheng">课程评价</div>
          </div>
        </template>
      </van-nav-bar>
    </div>
      <!-- =====课程列表===== -->
    <div class="ke_top">
      <div class="ke_title_icon">
        <div class="ke_title">{{this.title.title}}</div>
        <div
          :class="isShow?'iconfont icon-shoucang icon2':'iconfont icon-shoucang icon1'"
          @click="shou"
        ></div>
      </div>
      <div class="ke_mian">{{this.title.price | mianF}}</div>
      <div class="ke_free_num">
        <div class="ke_free">
          共{{this.title.is_free}}课时
          <span>|</span>
        </div>
        <div>{{this.title.sales_num}}人已报名</div>
      </div>
    </div>

    <div class="ke_teacher">
      <p class="ke_p">教学团队</p>
      <div v-for="(item,i) in teacher" :key="i">
        <div class="teacher_img">
          <img :src="item.avatar" />
        </div>
        <div class="teacher_name">{{item.teacher_name}}</div>
      </div>
    </div>

    <div class="ke_jie_da">
      <p class="ke_p">课程介绍</p>
      <div v-for="(item,i) in titles" :key="i">
        <div class="ke_cheng">{{item.periods_title}}</div>
      </div>
    </div>
    <div class="ke_jie">
      <p class="ke_cheng">课程大纲</p>
      <div v-for="(it,i) in titles" :key="i">
        <div class="cheng_title">{{it.periods_title}}</div>
      </div>
    </div>
    <!-- ======课程评价==== -->
    <div class="ping_da">
      <p class="ke_p">课程评论</p>
      <div v-for="(item,i) in ping" :key="i" class="jie_count">
        <div class="jia_img_name">
          <div class="jia_img">
            <img :src="item.avatar" />
          </div>
          <div>
            {{item.nickname}}
            <div>{{item.content}}</div>
          </div>
        </div>
      </div>
      <div >
        <div></div>
      </div>
    </div>

    <div class="detail_bottom" @click="liji">{{this.baoming.is_join_study | yiBao}}</div>

     <!-- ====模态框====== -->
    <div class="show_block_da" @click="xiaoshi" v-show="show" >
      <div class="show_block">
          <div class="fenxiang">分享</div>
          <div class="erwiema"><qriously :value="initQCode" :size="100"/></div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      flag: false,
      title: {},
      teacher: [],
      titles: [],
      ping: [],
      baoming:[],
      isShow: false,
      show: false,
      ID:"",
      type:"",
       initQCode: 'http://localhost:8080/#/Kecheng'
    };
  },
  methods: {
    //   吸顶事件
    handleTop() {
        console.log(window.pageYOffset)

        if(window.pageYOffset > 50) {
            this.flag = true;
        } else {
            this.flag = false
        }
      
      console.log(this.flag)
    },
    onClickLeft() {
      this.$router.push({ name: "Home" });
    },
    shou() {
      this.isShow = !this.isShow;
      this.$http
        .post("/api/app/collect", { course_basis_id: this.title.id, type: 1 })
        .then(res => {
          console.log(res);
        });
    },
    xiao(){
      this.show = true
    },
    xiaoshi(){
      this.show = false
    },
  async liji(){
    let { data:res}= await this.$http.post(`/api/app/order/downOrder`,{shop_id: this.ID,type:this.type})
      console.log(res)
      // console.log(this.ID,this.type)
    }
  },
  
  filters: {
    mianF(val) {
      if (val === 0) {
        return "免费";
      } else {
        return `￥：${(val / 100).toFixed(2)}`;
      }
    },
    yiBao(val){
      if(val === 1){
        return "立即学习"
      }else{
        return '立即报名'
      }
    }
  },
  created() {
      document.body.scrollTop = document.documentElement.scrollTop = 0;
  },
 
  mounted() {
    this.ID = this.$route.params.id,
    this.type = this.$route.params.type
    //   吸顶问题
    window.addEventListener("scroll", this.handleTop, true);
    this.$http
      .get(`/api/app/courseInfo/basis_id=${this.$route.params.id}?`)
      .then(res => {
        console.log(res.data.data.info);
        // console.log(res.data.data.teachers);
        this.title = res.data.data.info;
        this.baoming = res.data.data.info
        this.teacher = res.data.data.teachers;
      });
    this.$http
      .post("/api/app/courseChapter", { id: this.$route.params.id })
      .then(res => {
          // console.log(res.data.data)
        this.titles = res.data.data;
      });
    this.$http
      .post("/api/app/courseComment", {
        id: this.$route.params.id,
        limit: 10,
        page: 1
      })
      .then(res => {
        //   console.log(res.data.data.list)
        this.ping = res.data.data.list;
      });
  }
};
</script>

<style  scoped>
