  width: 100%;
  height: 3rem;
  border-radius: 10rem;
}
.teacher_name {
  font-size: 12px;
  color: gray;
  margin-top: 0.5rem;
  margin-left: 2rem;
}
.ke_p {
  margin-left: 1rem;
}
.ke_jie_da {
  width: 100%;
  height: 8rem;
