  margin-top: 1rem;
}
.ke_jie {
  width: 100%;
  height: 6rem;
  margin-top: 1rem;
  background-color: white;
}
.cheng_title {
  width: 80%;
  color: gray;
  margin-left: 2rem;
  margin-top: 0.5rem;
}
.ke_cheng {
  width: 70%;
  margin-left: 1rem;
  margin-top: 0.5rem;
}
.ke_title_icon {
  width: 100%;
  display: inline-flex;
  justify-content: space-around;
}
.iconfont {
  font-size: 25px;
}
.ping_da {
  width: 100%;
