}
.detail_bottom {
  width: 100%;
  height: 3rem;
  background-color: orangered;
  color: white;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  position: fixed;
  bottom: 0rem;
}
.jie_count {
  width: 100%;
